

class Toast {

    constructor() {
        this.init();
    }

    init() {

        this.main = $(this.getTemplate());
        $(document.body).append(this.main);

        this.contentElem = this.main.children('.toast-content');
    }

    getTemplate() {
        return `
            <div class="toast">
                <div class="toast-content">
                </div>
            </div>
        `;
    }

    show(useTimer) {
        let self = this;
        self.main.show();

        if (useTimer) {
            window.setTimeout(
                () => {
                    self.hide();
                },
                1000
            );
        }

    }

    hide() {
        this.main.hide();
    }

    setContent(content) {
        this.contentElem.html(content);
    }

    setSkin(skin) {
        this.contentElem
            .removeClass(this.lastClassName || '')
            .addClass(skin);

        this.lastClassName = skin;
    }

}

let _toast = new Toast();

export function toast (content='', skin='black', useTimer=true) {

    _toast.setContent(content);
    _toast.setSkin(skin);

    _toast.show(useTimer);
}

export function untoast() {
    _toast.hide();
}

